<template>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-8">
                <div class="card">
                    <div class="card-body">
                        <h5 class="text-center">Sistem Pakar Penyakit Kelamin</h5>
                        <hr>
                        <div class="alert alert-danger alert-dismissible fade show" v-if="this.flashing.status == true" role="alert">
                            Harap Lengkapi Form <strong>Nama</strong> dan <strong>Alamat</strong>
                        </div>
                        <div class="form-group">
                            <input type="text" v-model="form.nama" placeholder="Nama Lengkap" :class="this.flashing.status == true ? 'is-invalid form-control w-75' : 'form-control w-75' ">
                        </div>
                        <div class="form-group mt-3">
                            <textarea v-model="form.alamat" placeholder="Masukan Alamat Lengkap" :class="this.flashing.status == true ? 'is-invalid form-control' : 'form-control'"></textarea>
                        </div>    
                    </div>
                </div>
                <div class="card mt-3" v-if="penyakit.length > 0">
                    <div class="card-body text-start" v-if="this.penyakit.length != this.hasil1.length">
                        <h5 v-if="penyakit.length > 0">Pilih <strong>Ya atau Tidak</strong> Pada pertanyaan berikut ini</h5>
                        <hr>
                        <p>{{ penyakit[counter].nama_gejala }}</p>
                        <div class="form-check">
                            <input type="radio" value="ya" required name="question" v-model="form.question" class="form-check-input" id="question">
                            <label for="ya"  class="form-check-label">Ya</label>
                        </div>
                        <div class="form-check">
                            <input type="radio" value="tidak" required name="question" v-model="form.question" class="form-check-input" id="question">
                            <label for="ya" class="form-check-label">Tidak</label>
                        </div>

                        <a  v-if="counter+1 < penyakit.length"  v-on:click="pushData(penyakit[counter].nama_gejala,form.question,form.nama,form.alamat)" class="btn btn-outline-primary mt-1">Next <i class="fas fa-arrow-right"></i></a>
                        <button @click="getDiagnosa(penyakit[counter].nama_gejala,form.question,form.nama,form.alamat)" v-if="counter+1 >= penyakit.length" class="btn btn-outline-primary">Lihat Hasil Diagnosa <i class="fas fa-print"></i></button>
                    </div>
                    <div class="card-body text-start" v-else id="cetak">
                        <h3 class="text-center">Sistem Pakar Penyakit Kelamin</h3>
                        <hr>
                        <h5>Berikut ini Hasil Diagnosa anda : </h5>
                        <div class="row justify-content-center">
                            <p>Nama Lengkap : <strong>{{ this.form.nama }}</strong></p>
                            <p>Alamat       : <strong>{{ this.form.alamat}}</strong></p>
                            <div class="col-md-12 justify-content-center">
                                <table class="table">
                                    <thead class="bg-primary text-light">
                                        <tr>
                                            <th>No</th>
                                            <th>Penyakit</th>
                                            <th>Presentase</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(x,index) in this.final_result" :key="x.nilai_cf">
                                            <td>{{index+1}}</td>
                                            <td>{{ x.nama_penyakit }}</td>
                                            <td><strong>{{ Math.round(x.nilai_cf) }}  %</strong></td>
                                        </tr>
                                    </tbody>
                                </table>
                                <p>Dari Tabel diatas dapat di tarik kesimpulan bahwa berdasarkan hasil masukan gejala yang anda telah pilih, diperoleh hasil diagnosa awal yang menyatakan kemungkinan terdiagnosa mengidap penyakit <strong>{{ this.result_diagnosa[0].penyakit_diagnosa }}</strong> memiliki nilai CF tertinggi yaitu <strong>{{this.result_diagnosa[0].nilai_diagnosa}} %</strong></p>
                                <hr>
                                <div v-html="this.result_diagnosa[0].desc">
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer text-start" v-if="this.result_diagnosa[0].nilai_diagnosa > 0">
                        <button class="btn btn-outline-success" v-on:click="cetak(this.form.nama,this.form.alamat)">cetak PDF <i class="fas fa-print"></i></button>
                    </div>
                </div>
            </div>    
        </div>
    </div>
</template>
<script>
import axios from '../url/base_url'
import lodash from "lodash"
export default {
    name:"Form",
    data()
    {
        return{
            flashing:{
                status:false
            },
            counter:0,
            form:{
                nama:"",
                alamat:"",
                nama_penyakit:null,
                nama_gejala:null,
                nilai_cf:null,
                question:null
            },
            data_penyakit:[],
            penyakit:[{
                nama_penyakit:"",
                nama_gejala:"",
                nilai_cf:"",
                question:""
            }],
            // hasil dari data passien
            hasil1:[],
            // hasil dari mendapatkan data nilai cf
            hasil2:[],
            // hasil perhitungan akhir
            final_result:[],
            // diagnosa_full
            result_diagnosa:[
                {
                    nama_passien:"",
                    alamat_passien:"",
                    penyakit_diagnosa:"",
                    nilai_diagnosa:0,
                    desc:""
                }
            ],
            result_gonore:null,
        }
    }, 
    mounted()
    {
        this.getData()
        this.getPenyakit()
    },
    methods:{
        reGroup(list,key)
        {
            const newGroup = {};
            list.forEach(item => {
                const newItem = Object.assign({}, item);
                delete newItem[key];
                newGroup[item[key]] = newGroup[item[key]] || [];
                newGroup[item[key]].push(newItem);
            });
            return newGroup
        },
        async getPenyakit()
        {
            await axios.get("data_penyakit").then((r)=>{
                this.data_penyakit = r.data
            }).catch((e)=>{
                console.log(e)
            })
        },
        async pushData(gejala,ask,nama,alamat)
        {
            if(nama == "" || alamat == "")
            {
                this.flashing.status = true
            }else{
                this.flashing.status = false
                this.counter+=1
                const send  = {
                    gejala:gejala,
                    question:ask
                }
                this.form.question = ""
                this.hasil1.push(send)
            }
            
        },
        async getDiagnosa(gejala1,ask1,nama,alamat)
        {
            if(nama == "" || alamat == "")
            {
                this.flashing.status = true
            }else{
                this.flashing.status = false
                const send = {
                    gejala:gejala1,
                    question:ask1
                }
                this.hasil1.push(send)
                const sendRequest = new FormData()
                sendRequest.append('data',JSON.stringify(this.hasil1));
                await axios.post('',sendRequest).then((response) => {
                    this.hasil2 = response.data
                    this.hasil2.reduce((acc,curr) => {
                        if(!acc[curr.penyakit])
                        {
                            acc[curr.penyakit] = {
                                nama_penyakit:curr.penyakit,
                                description:curr.description,
                                nilai_cf:0,
                            }
                            this.final_result.push(acc[curr.penyakit])
                        }
                        acc[curr.penyakit].nilai_cf+=curr.nilai_cf / parseFloat(curr.gt) * 100
                        return acc
                    },{})
                    console.table(this.final_result)
                    // push ke data cetak
                    const result_final = (lodash.maxBy(this.final_result,'nilai_cf'))
                    this.result_diagnosa[0].nama_passien = this.form.nama
                    this.result_diagnosa[0].alamat_passien = this.form.alamat
                    this.result_diagnosa[0].penyakit_diagnosa = result_final.nama_penyakit
                    this.result_diagnosa[0].nilai_diagnosa = Math.round(result_final.nilai_cf)
                    this.result_diagnosa[0].desc = result_final.description
                    console.log(this.result_diagnosa)
                }).catch((error) => {
                    console.log(error)
                })
            }
        },
        async cetak(nama,alamat)
        {
            if(nama == "" || alamat == "")
            {
                this.flashing.status = true
            }else{
                let data = lodash.maxBy(this.final_result,'nilai_cf')
                this.flashing.status = false
                const newForm = new FormData()
                newForm.append("nama",this.form.nama)
                newForm.append("penyakit",data.nama_penyakit)
                newForm.append("alamat",this.form.alamat)
                newForm.append("presentase",Math.round(data.nilai_cf))
                await axios.post('post_passien',newForm).then((response) => {
                    var getId = document.querySelector('#cetak').innerHTML;
                    var a = window.open('','','height=500,width=500')
                    a.document.write('<html>')
                    a.document.write('<body>')
                    a.document.write(getId)
                    a.document.write('</body></html>')
                    a.print()
                    a.document.close()
                }).catch((error) => {
                    console.log(error)
                })
            }
            

        },
        async getData()
        {
            await axios.get("data_pertanyaan").then((response) => {
                this.penyakit = response.data
            }).catch((error) => {
                console.log(error)
            })
        }
    }
}
</script>
<style scoped>
    .container{
        margin-top: 80px;
    }
</style>